h1, h2, h3,
h4, h5, h6,
p, button, ul, li {
   padding: 0;
   margin: 0;
   list-style: none;
}

.mlp-content__result {
   max-width: 420px;
   margin: 5% auto 0;

   .mlp-content-result__info {
      box-shadow: 0 0 15px -8px rgba(0, 0, 0, 0.75);
      margin: 2px;
      background: #fff;
      padding: 12px 12px 16px;

      .mlp-content-result__info-container {
         margin-bottom: 25px;
      }

      .mlp-content-result-info__img {
         img {
            width: 100%;
         }
      }

      .mlp-content-result-info__item {
         margin-top: 10px;

         .mlp-content-result-info-item__title-1,
         .mlp-content-result-info-item__title-3 {
            font-size: 20px !important;
            margin-bottom: 10px;
            font-weight: 600;
            line-height: 1.3;
         }

         .mlp-content-result-info-item__field-input-2 {
            margin-bottom: 4px;
         }

         .mlp-content-result-info-item__title-2,
         .mlp-content-result-info-item__title-4 {
            font-weight: 400;
            margin-bottom: 4px;
            font-size: 15px;
         }

         .mlp-content-result-info-item__field {
            align-items: center;
            display: flex;


            p {
               font-size: 15px;
               width: 200px;
            }

            input {
               font-size: 15px;

               &:disabled {
                  cursor: inherit;
                  background: #fff;
               }
            }
         }
      }
   }

   .mlp-content-result__action {
      margin: 20px 0;
      text-align: center;
   }

   .mlp-content-result__error {
      color: tomato;
      cursor: pointer;
      text-align: center;

      &:hover {
         text-decoration: underline;
      }
   }

   .mlp-content-result__socials {
      display: flex;
      border-radius: 5px;
      overflow: hidden;

      .mlp-content-result-socials__item {
         text-align: center;
         color: #fff;
         font-size: 2px;
         padding: 8px 0 5px;
         width: 100%;
         box-sizing: border-box;
         cursor: pointer;
         transition: all 0.3s;
         display: block;

         svg {
            width: 24px;
            height: 24px;
         }

         .mlp-content-result-socials-item__title {
            font-size: 14px;
         }
      }

      .mlp-content-result-socials__item-vk {
         background: #4A729F;

         &:hover {
            background: lighten(#4A729F, 8%);
         }
      }

      .mlp-content-result-socials__item-telegram {
         background: #2FA7DB;

         &:hover {
            background: lighten(#2FA7DB, 8%);
         }
      }

      .mlp-content-result-socials__item-facebook {
         background: #0293FF;

         &:hover {
            background: lighten(#0293FF, 8%);
         }
      }

      .mlp-content-result-socials__item-whatsapp {
         background: #3FE15C;

         &:hover {
            background: lighten(#3FE15C, 8%);
         }
      }
   }

   .mlp-content-result__footer {
      margin-top: 30px;
      display: flex;
      align-items: center;
      font-size: 12px;
      justify-content: space-between;

      .mlp-content-result__terms {
         span {
            text-decoration: underline;
            cursor: pointer;
         }
      }

      .mlp-content-result__link {
         color: #7e7e7e;

         a {
            &:hover {
               text-decoration: underline;
            }

            &:active {
               text-decoration: underline;
            }
         }

         span {
            color: tomato;
         }
      }
   }
}

.mlp-terms-popup-content {
   .ant-modal {
      width: 60% !important;

      .popup-content {
         font-size: 16px;

         h2 {
            font-size: 22px;
            margin: 20px 0;
            font-weight: 600;
         }

         h3 {
            font-size: 20px;
            font-weight: 600;
            margin: 13px 0;
         }

         ol, ul, {
            margin-left: 17px;
         }

         ul {
            list-style-type: initial;
            margin: 13px 17px;
         }

         .popup-content-ol {
            list-style-type: lower-alpha;
         }
      }
   }
}